/* Bounce In */
@mixin bounce-in {
	@include hacks();
	@include prefixed(transition-duration, $slowDuration);

	&:hover,
	&:focus,
	&:active {
		@include prefixed(transform, scale(1.2));
		@include prefixed(transition-timing-function, cubic-bezier(.47,2.02,.31,-0.36));
	}
}



